.aside-article-title {
  font-family: "Roboto Slab", serif;
}

.section-title, .article-title h1, .side-main-heading, .side-heading, .footer-adr-heading, .footer-nav-heading {
  font-family: "Roboto Slab", serif;
}

.tech-section, .biz-section, .sci-section, .spo-section {
  padding: 3rem 0.5rem;
}

