* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans", sans-serif;
  color: #2C3E50;
  font-weight: 300;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

ul {
  list-style-type: none;
  padding: 0;
}

select {
  width: 100%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  padding: 1rem;
  font-size: 1rem;
  color: #2C3E50;
  border: 1px solid #E74C3C;
  border-radius: none;
}

select:focus {
  outline: none;
}

@media (min-width: 600px) {
  select {
    width: inherit;
  }
}

.rtl-direction {
  direction: rtl;
  unicode-bidi: bidi-override;
}

.ltr-direction {
  direction: ltr;
  unicode-bidi: normal;
}

.hide {
  display: none;
}

.body {
  background-color: #ECF0F1;
}

.row:before, .row:after {
  content: "";
  display: table;
}

.row:after {
  clear: both;
}

.container {
  position: relative;
  width: 100%;
}

@media (min-width: 900px) {
  .container {
    width: 90%;
    margin: 0 auto;
  }
}

.side-container {
  width: 100%;
  padding: 0.5rem;
  background-color: #2980B9;
}

.header {
  min-height: 80px;
  position: relative;
  width: 100%;
  background-color: #2980B9;
}

@media (min-width: 1200px) {
  .header {
    min-height: 145px;
  }
}

.header-container {
  max-height: 80px;
  position: fixed;
  width: 100%;
  height: 100%;
  padding: 0.5rem;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background-color: #2980B9;
  transition: all 0.5s ease;
}

.header-container.sticky .header-logo {
  width: 3rem;
}

@media (max-width: 599px) and (orientation: landscape) {
  .header-container.sticky .header-logo {
    width: 3rem;
  }
}

@media (min-width: 900px) {
  .header-container.sticky .header-tag-line {
    width: 43%;
    padding: 0.8rem;
  }
  .header-container.sticky .header-tag-line h1 {
    font-size: 1.0rem;
    line-height: 1.6rem;
  }
}

@media (min-width: 1200px) {
  .header-container.sticky .header-tag-line {
    width: 30%;
  }
}

@media (min-width: 1440px) {
  .header-container.sticky .header-tag-line {
    width: 25%;
  }
}

.header-container.sticky .mob-nav-btn {
  top: -7px;
}

@media (min-width: 900px) {
  .header-container.sticky .main-nav {
    padding: 0.5rem 0;
  }
}

@media (min-width: 1200px) {
  .header-container {
    max-height: 145px;
  }
}

.header-logo {
  width: 4rem;
  float: left;
  transition: all 0.5s ease;
}

.header-logo img {
  width: 100%;
  height: auto;
}

@media (max-width: 599px) and (orientation: landscape) {
  .header-logo {
    width: 4.25rem;
  }
}

@media (min-width: 1200px) {
  .header-logo {
    width: 8rem;
  }
}

.header-tag-line {
  display: none;
  width: 43%;
  float: left;
  padding: 1rem;
}

.header-tag-line h1 {
  margin: 0;
  text-align: center;
  line-height: 2.8rem;
  font-weight: 300;
  color: #FFFFFF;
}

@media (min-width: 900px) {
  .header-tag-line {
    display: block;
    width: 41%;
    padding: 0.5rem;
  }
  .header-tag-line h1 {
    font-size: 1.2rem;
    line-height: 1.6rem;
  }
}

@media (min-width: 990px) {
  .header-tag-line {
    width: 33%;
  }
}

@media (min-width: 1200px) {
  .header-tag-line {
    width: 45%;
    padding: 1.5rem;
  }
  .header-tag-line h1 {
    font-size: 2.0rem;
    line-height: 2.6rem;
  }
}

@media (min-width: 1440px) {
  .header-tag-line {
    width: 35%;
    padding: 1.5rem 0.5rem;
  }
}

.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 70%;
  height: 100%;
  background-color: #2980B9;
  transform: translateX(-100%);
  transition: all 0.5s ease;
}

@media (min-width: 900px) {
  .main-nav {
    display: block;
    width: 53%;
    height: inherit;
    transform: translateX(0);
    position: absolute;
    right: 0;
    left: inherit;
    bottom: inherit;
    padding: 1rem 0;
  }
}

@media (min-width: 1200px) {
  .main-nav {
    width: 45%;
    padding: 3rem 0;
  }
}

.show-nav {
  transform: translate(0);
}

.mob-nav-btn {
  display: inline-block;
  position: absolute;
  top: 3px;
  right: 20px;
  cursor: pointer;
  background-color: transparent;
  padding: 10px;
}

@media (min-width: 900px) {
  .mob-nav-btn {
    display: none;
  }
}

.mob-nav-1.animate {
  transform: rotate(-225deg) translate(9px, -9px);
}

.mob-nav-2.animate {
  opacity: 0;
}

.mob-nav-3.animate {
  transform: rotate(225deg) translate(6px, 7px);
}

.mob-nav-1, .mob-nav-2, .mob-nav-3 {
  width: 40px;
  height: 5px;
  margin: 6px 0;
  background-color: #fff;
  transition: 0.7s;
}

.navbar-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  margin: 0;
}

.navbar-nav li {
  display: inline-block;
  padding: 1rem 0;
}

.navbar-nav li a {
  padding: 0.5rem 0.75rem;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar-nav li a:hover {
  background-color: #ECF0F1;
  color: #E74C3C;
}

@media (min-width: 900px) {
  .navbar-nav li a {
    font-size: 1.0rem;
    transition: all 0.5s;
  }
}

@media (min-width: 900px) {
  .navbar-nav li {
    padding: 0.5rem 0;
  }
}

@media (min-width: 900px) {
  .navbar-nav {
    display: block;
    float: right;
  }
}

.sticky {
  max-height: 60px;
}

.main-block {
  width: 100%;
}

@media (min-width: 900px) {
  .main-block {
    float: left;
    width: 70%;
  }
}

.section-title {
  color: #2C3E50;
}

.section-content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media (min-width: 600px) {
  .section-content {
    flex-direction: row;
  }
}

.single-article {
  width: 100%;
  margin: 2rem 0;
}

@media (min-width: 600px) {
  .single-article {
    width: 49%;
    float: left;
  }
}

@media (min-width: 1200px) {
  .single-article {
    width: 33%;
  }
}

@media (min-width: 1440px) {
  .single-article {
    width: 30%;
  }
}

.article-link {
  color: initial;
}

.article-link:hover .article-title h1 {
  color: #E74C3C;
}

.article-link:hover .article-img img {
  transform: scale(1.1);
}

.article-img {
  width: 100%;
  overflow: hidden;
}

.article-img img {
  width: 100%;
  height: auto;
  transition: all 0.5s;
}

.article-title {
  padding: 0 0.25rem;
}

.article-title h1 {
  color: #2C3E50;
  font-size: 1.5rem;
  transition: all 0.5s;
}

.article-desc {
  padding: 0 0.25rem;
}

.article-desc p {
  font-size: 1.0rem;
  line-height: 1.7rem;
  font-weight: 400;
}

.article-aut p {
  margin: 0.25rem 0;
  font-size: 0.8rem;
}

.right-block {
  width: 100%;
}

@media (min-width: 900px) {
  .right-block {
    width: 30%;
    float: left;
    margin-top: 3rem;
  }
}

.side-main-heading {
  color: #FFFFFF;
  font-weight: 300;
  margin: 0.5rem 0;
}

.popular-art {
  width: 100%;
}

.side-heading {
  color: #FFFFFF;
  font-weight: 300;
  margin: 1rem 0 0.5rem 0;
}

.aside-divider {
  width: 100%;
}

.pop-art {
  width: 100%;
}

.aside-article-link {
  color: #FFFFFF;
}

.aside-article-link:hover .aside-article-title {
  color: #E74C3C;
}

.aside-article-img {
  width: 100%;
  height: auto;
}

.aside-article-title {
  margin: 0.5rem 0 0.25rem 0;
  font-size: 1.6rem;
  line-height: 2.1rem;
  transition: color, 0.5s;
}

@media (min-width: 900px) {
  .aside-article-title {
    font-size: 1.4rem;
    line-height: 2.0rem;
  }
}

.aside-article-content {
  font-size: 0.9rem;
  line-height: 1.5rem;
}

.tech {
  min-height: 600px;
}

.tech a {
  text-decoration: none;
  color: initial;
}

.footer {
  width: 100%;
  padding: 2rem 0 1rem 0;
  background-color: #2C3E50;
  color: #FFFFFF;
}

.address-block, .footer-nav {
  width: 100%;
  text-align: center;
}

@media (min-width: 600px) {
  .address-block, .footer-nav {
    width: 50%;
    float: left;
    padding: 0 1rem;
    text-align: left;
  }
}

@media (min-width: 900px) {
  .address-block, .footer-nav {
    width: 30%;
  }
}

.footer-adr-heading, .footer-nav-heading {
  font-size: 1.7rem;
}

.footer-adr {
  font-style: normal;
  font-size: 0.9rem;
}

.footer-adr ul li {
  margin: 0.5rem 0;
}

.footer-adr ul li:first-child {
  font-size: 2rem;
  font-weight: 400;
}

.footer-menu {
  font-weight: 300;
}

.footer-menu li {
  padding: 0.5rem 0;
}

.footer-menu li a {
  padding: 0.25rem;
  transition: all 0.5s;
}

.footer-menu li a:hover {
  background-color: #ECF0F1;
  color: #E74C3C;
}

.footer-copy {
  text-align: center;
  font-size: 0.6rem;
}

@media (min-width: 600px) {
  .footer-copy {
    font-size: 0.8rem;
  }
}
